<script setup>
import { ref, onMounted } from 'vue';

let list = {
  'edu': ref([]),
  'job': ref([]),
  'proj': ref([])
};
const isLoading = ref(true);

onMounted(async() => {
  const [eduRes, jobRes] = await Promise.all([
    fetch('http://localhost:3000/eduList'),
    fetch('http://localhost:3000/jobList')
  ]);
  list['edu'].value = await eduRes.json();
  list['job'].value = await jobRes.json();
  isLoading.value = false;
})
</script>


<template>
  <!-- <h1>profile</h1> -->
  <!-- <div class='celibrate'>
    <Celibrate msg/>
  </div> -->
  <div v-if='isLoading'>loading...</div>
  <div v-else>
    <div v-for="edu in list['edu'].value" :key='edu.id'>
      <h2>{{ edu.inst }}</h2>
      <p>{{ edu.start }} - {{ edu.end }}</p>
    </div>
  </div>
  <div v-if='isLoading'>loading...</div>
  <div v-else>
    <div v-for="job in list['job'].value" :key='job.id' class='job'>
      <router-link :to='{ name: "JobDetial", params: { id: job.id }}'>
        <h2>{{ job.comp }}</h2>
        <p>{{ job.start }} - {{ job.end }}</p>
      </router-link>
    </div>
  </div>
</template>

